<template>
    <div class="carousel-box">
        <v-layout wrap align-center>
        <v-carousel hide-controls class="carousel-controls-bg">
            <v-carousel-item
            v-for="(item,i) in items"
            :key="i"
            :src="item.src"
            ></v-carousel-item>
        </v-carousel>
        </v-layout>
    </div>
</template>
<script>
  export default {
    data () {
      return {
        items: [
          {
            src: 'http://abhipri.com/images/abhipri-home-banner-3.png'
          },
          {
            src: 'http://abhipri.com/images/abhipri-home-banner-2.png'
          },
          {
            src: 'http://abhipri.com/images/abhipri-home-banner-1.png'
          }
        ]
      }
    }
  }
</script>
<style scoped>
.carousel-box {
 border-bottom: solid 9px #bf9e56;
}
</style>



